import { TreeView, TreeItem } from "@material-ui/lab";
import { MdExpandLess, MdExpandMore } from "react-icons/md";
import uuid from "uuid/v4";

const toTreeItems = object => {
  switch (typeof object) {
    case "object": {
    }
    default: {
      return <TreeItem nodeId={uuid()} label={object.toString()} />;
    }
  }

  const entries = Object.entries(object);

  return entries.map(([key, value]) => {
    switch (typeof key) {
    }

    switch (typeof value) {
      case "number":
        return <TreeItem nodeId={uuid()} label={key}></TreeItem>;
      case "string":
        return (
          <TreeItem nodeId={uuid()} label={key}>
            <TreeItem nodeId={uuid()} label={value} />
          </TreeItem>
        );
      case "object":
        if (JSON.stringify(value) === "{}") return <></>;
        if (Array.isArray(value)) {
          const arrayEntries = Object.values(value);
          return arrayEntries.map(toTreeItems);
        } else
          return (
            <TreeItem nodeId={uuid()} label={key}>
              {toTreeItems(value)}
            </TreeItem>
          );
      default:
        return (
          <TreeItem nodeId={uuid()} label={key}>
            <TreeItem nodeId={uuid()} label={`UNKNOWN TYPE: ${typeof value}`} />
          </TreeItem>
        );
    }
  });
};

export const ObjectTree = ({ object }) => (
  <TreeView
    defaultCollapseIcon={<MdExpandMore />}
    defaultExpandIcon={<MdExpandLess />}
  >
    {toTreeItems(object)}
  </TreeView>
);
